<template>
  <div class="editer">
    <b-card
      title="Card Title"
      sub-title="Card Subtitle"
      style="max-width: 20rem"
    >
      <b-tabs>
        <b-tab title="Tab 1">
          <b-card-text>Tab 1 Contents</b-card-text>
        </b-tab>
        <b-tab title="Tab 2">
          <b-card-text>Tab 2 Contents</b-card-text>
          <b-button size="md" variant="primary"> Button </b-button>
        </b-tab>
        <b-tab title="Tab 3" disabled>
          <b-card-text>Tab 3 Contents</b-card-text>
        </b-tab>
      </b-tabs>
    </b-card>
    <form v-on:submit.prevent="insertData">
      <div><label>Nom</label> <input type="text" v-model="vuejs.nom" /></div>
      <div>
        <label>Prenom</label><input type="text" v-model="vuejs.prenom" />
      </div>
      <div>
        <label>Adresse</label><input type="text" v-model="vuejs.adresse" />
      </div>
      <div><label>Email</label><input type="text" v-model="vuejs.email" /></div>
      <div>
        <label>Password</label
        ><input type="password" v-model="vuejs.password" />
      </div>
      <div>
        <button type="submit">Envoyer</button>
        <button class="submit">Annuler</button>
      </div>
    </form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      vuejs: {},
    };
  },
  methods: {
    insertData() {
      console.log(this.vuejs);

      var dataInsert = {
        nom: this.vuejs.nom,
        prenom: this.vuejs.prenom,
        adresse: this.vuejs.adresse,
        email: this.vuejs.email,
        password: this.vuejs.password,
      };
      fetch("http://localhost:8000/vuejs/?insert=1", {
        method: "POST",
        body: JSON.stringify(dataInsert),
      })
        .then((res) => res.json())
        .then((data) => {
          console.log(data);
          window.location.href = "/creer";
        });
    },
  },
};
</script>
