<template>
  <div class="editer" v-for="datas in vuejs" :key="datas.id">
    <div>{{ datas.nom }}</div>
    <div>{{ datas.prenom }}</div>
    <div>{{ datas.adresse }}</div>
    <div>{{ datas.email }}</div>
    <div class="boutton">
      <router-link :to="{ name: 'Modifier', params: { id: datas.id } }"
        >Editer</router-link
      >
      <button type="button" v-on:click="deleteList(datas.id)">Suppriner</button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      vuejs: [],
    };
  },
  created: function () {
    this.listData();
  },
  methods: {
    listData() {
      fetch("http://localhost:8000/vuejs/")
        .then((res) => res.json())
        .then((data) => {
          console.log(data);
          this.vuejs = [];
          if (typeof data[0].success === "undefined") {
            this.vuejs = data;
          }
        })
        .catch(console.log());
    },
    deleteList(id) {
      console.log(id);
      fetch("http://localhost:8000/vuejs/?delete=" + id)
        .then((res) => res.json())
        .then((data) => {
          console.log(data);
          window.location.href = "/creer";
        })
        .catch(console.log());
    },
    editList(id) {
      console.log(id);
    },
  },
};
</script>
