<template>
  <div class="modifier">
    <form v-on:submit.prevent="updateList">
      <div><label>Nom</label> <input type="text" v-model="vuejs.nom" /></div>
      <div>
        <label>Prenom</label><input type="text" v-model="vuejs.prenom" />
      </div>
      <div>
        <label>Adresse</label><input type="text" v-model="vuejs.adresse" />
      </div>
      <div><label>Email</label><input type="text" v-model="vuejs.email" /></div>
      <div>
        <label>Password</label
        ><input type="password" v-model="vuejs.password" />
      </div>
      <div>
        <button type="submit">Modifier</button>
        <button class="submit">Annuler</button>
      </div>
    </form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      vuejs: {},
    };
  },
  created: function () {
    this.getListId();
  },
  methods: {
    getListId() {
      fetch("http://localhost:8000/vuejs/?data=" + this.$route.params.id)
        .then((res) => res.json())
        .then((data) => {
          console.log(data);
          this.vuejs = data[0];
        })
        .catch(console.log());
    },
    updateList() {
      console.log(this.vuejs);

      var dataInsert = {
        id: this.$route.params.id,
        nom: this.vuejs.nom,
        prenom: this.vuejs.prenom,
        adresse: this.vuejs.adresse,
        email: this.vuejs.email,
        password: this.vuejs.password,
      };
      fetch("http://localhost:8000/vuejs/?update=" + this.$route.params.id, {
        method: "POST",
        body: JSON.stringify(dataInsert),
      })
        .then((res) => res.json())
        .then((data) => {
          console.log(data);
          window.location.href = "/creer";
        });
    },
  },
};
</script>
